.columns {
	@include ltr-property("margin", (-$column-gap), false);
	@include ltr-property("margin", (-$column-gap));
	margin-top: (-$column-gap);
	&:last-child {
		margin-bottom: (-$column-gap);
	}
	&:not(:last-child) {
		margin-bottom: calc(1.5rem - #{$column-gap});
	}
	// Modifiers
	&.is-centered {
		justify-content: center;
	}
	&.is-gapless {
		@include ltr-property("margin", 0, false);
		@include ltr-property("margin", 0);
		margin-top: 0;
		& > .column {
			margin: 0;
			padding: 0 !important;
		}
		&:not(:last-child) {
			margin-bottom: 1.5rem;
		}
		&:last-child {
			margin-bottom: 0;
		}
	}
	&.is-mobile {
		display: flex;
	}
	&.is-multiline {
		flex-wrap: wrap;
	}
	&.is-vcentered {
		align-items: center;
	}
	// Responsiveness
	@include tablet {
		&:not(.is-desktop) {
			display: flex;
		}
	}
	@include desktop {
		// Modifiers
		&.is-desktop {
			display: flex;
		}
	}
}
